import React, { useState, useEffect } from "react";
import { useHistory, useParams } from "react-router-dom";
import styled from "styled-components";
import "./Detail.scss";

let Boxing = styled.div`
  padding-top: 20px;
`;

let Titles = styled.h4`
  font-size: 30px;
  color: ${(props) => props.색상};
`;

function Detail(props) {
  let [alert, alertState] = useState(true);
  let [input, inputState] = useState("");
  useEffect(() => {
    let timeOut = setTimeout(() => {
      alertState(false);
    }, 2000);
    return () => {
      clearTimeout(timeOut);
    };
  }, [alert]);

  let { id } = useParams();
  let history = useHistory();
  let findItem = props.toy.find((x) => x.id == id);
  return (
    <div className="container">
      <Boxing>
        <Titles className="red">Detail</Titles>
      </Boxing>
      {input}
      <input
        onChange={(e) => {
          inputState(e.target.value);
        }}
      />

      {alert === true ? (
        <div className="my-alert2">
          <p>재고가 얼마 남지 않았습니다.</p>
        </div>
      ) : null}

      <div className="row">
        <div className="col-md-6">
          <img src={findItem.src} width="100%" />
        </div>
        <div className="col-md-6 mt-4">
          <h4 className="pt-5">{findItem.title}</h4>
          <p>{findItem.content}</p>
          <p>{findItem.price}</p>
          <Info 재고={props.재고}></Info>
          <button
            className="btn btn-primary"
            onClick={() => {
              props.재고변경([9, 10, 11]);
            }}
          >
            주문하기
          </button>
          &nbsp;
          <button
            className="btn btn-danger"
            onClick={() => {
              history.goBack();
            }}
          >
            뒤로가기
          </button>
        </div>
      </div>
    </div>
  );
}

function Info(props) {
  return <p>재고 : {props.재고[0]}</p>;
}

export default Detail;
