import React, { useEffect, useState } from "react";
import logo from "./logo.svg";
import {
  Navbar,
  Container,
  Nav,
  NavDropdown,
  Button,
  Row,
  Col,
} from "react-bootstrap";
import "./App.css";
import Data from "./data.js";
import { func } from "prop-types";
import Detail from "./Detail.js";
import axios from "axios";

import { Link, Route, Switch } from "react-router-dom";

function App() {
  let [toy, toy변경] = useState(Data);
  let [재고, 재고변경] = useState([10, 11, 12]);

  return (
    <div className="App">
      <Navbar bg="light" expand="lg">
        <Container>
          <Navbar.Brand href="#home">Puppy Toy Shop</Navbar.Brand>
          <Navbar.Toggle aria-controls="basic-navbar-nav" />
          <Navbar.Collapse id="basic-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/">
                Home
              </Nav.Link>
              <Nav.Link as={Link} to="/detail">
                Detail
              </Nav.Link>
              <NavDropdown title="Dropdown" id="basic-nav-dropdown">
                <NavDropdown.Item href="#action/3.1">Action</NavDropdown.Item>
                <NavDropdown.Item href="#action/3.2">
                  Another action
                </NavDropdown.Item>
                <NavDropdown.Item href="#action/3.3">
                  Something
                </NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item href="#action/3.4">
                  Separated link h
                </NavDropdown.Item>
              </NavDropdown>
            </Nav>
          </Navbar.Collapse>
        </Container>
      </Navbar>
      <Switch>
        <Route exact path="/">
          <div className="Jumbotron">
            <div className="MyJumbo">
              <h1>20% Season Off</h1>
              <p>룰루랄라 신나게 어쩌고 저쩌고~~</p>
              <p>
                <Button variant="primary">Learn more</Button>
              </p>
            </div>
          </div>

          <Container>
            <Row>
              {toy.map((data, i) => {
                return <Card toy={data} i={i} key={i} />;
              })}
            </Row>
            <button
              className="btn btn-primary"
              onClick={() => {
                axios
                  .get("https://codingapple1.github.io/shop/data2.json")
                  .then((result) => {
                    console.log(result.data);
                    toy변경([...toy, ...result.data]);
                  })
                  .catch(() => {
                    console.log("실패함");
                  });
              }}
            >
              더보기
            </button>
          </Container>
        </Route>

        <Route path="/detail/:id">
          <Detail toy={toy} 재고={재고} 재고변경={재고변경} />
        </Route>
        {/* <Route path="/" component={Modal}></Route> */}

        <Route path="/:id">
          <div>아무거나 적었을 때 이거 보여주기</div>
        </Route>
      </Switch>
    </div>
  );
}

function Card(props) {
  return (
    <Col sm>
      <img src={`${props.toy.src}`} width="100%"></img>
      <h4>{props.toy.title}</h4>
      <p>{props.toy.content}</p>
      <p>{props.toy.price}</p>
    </Col>
  );
}

export default App;
